  computed: {
    arrFn() {
      let data = [];
      this.arr.forEach(item => {
        data = [...data, ...item.one, ...item.two];
      });
      
      data.forEach(item => {
        if (item.lable != undefined) {
          item.text =
            this.infoSuObj[item.key] == null ||
            this.infoSuObj[item.key] == undefined
              ? 0 + item.lable
              : this.infoSuObj[item.key] + item.lable;
        } else {
          if (item.key.indexOf("Time") != -1) {
            item.text = dateTime(this.infoSuObj[item.key]);
          } else if(item.title==='经纪人'){
            item.text=this.infoSuObj[item.key]+this.infoSuObj.agentPhone
          }else {
            item.text = this.infoSuObj[item.key];
          }
        }
      });
      return this.arr;
    },
    //成交
    arrsFn(){
       let data = [];
      this.arrs.forEach(item => {
        data = [...data, ...item.one, ...item.two];
      });
      let type={
        '1':'已提交',
        '2':'待审核',
        '4':'已拒绝',
        '3':'已通过'
      }
      data.forEach(item => {
        if (item.lable != undefined) {
             item.text =
            this.infoSuObj[item.key] == null ||
            this.infoSuObj[item.key] == undefined
              ? 0 + item.lable
