      this.$confirm("确认提交, 是否继续?", "提示", {
        confirmButtonText: "是",
        cancelButtonText: "否",
        type: "warning"
      }).then(() => {
        commitTicket({ ticketId: this.infoSuObj.ticketId }).then(res => {
          if (res.data.code === "000") {
            this.$parent.handleClick(this.infoSuObj);
            this.$parent.onSubmit();
            this.$message.success("提交成功");
          }
        });
      });
    },
