<style lang="scss">
.infoSuccess {
  .clearfix {
    position: relative;
    &::before {
      content: "";
      height: 20px;
      width: 5px;
      background-color: #000;
      position: absolute;
      left: 10px;
      top: 50%;
      transform: translateY(-50%);
    }
    span {
      font-weight: 900;
      font-size: 26px;
      padding-left: 30px;
    }
  }
  .miss_down {
      display: flex;
      justify-content: center;
      margin-top: 70px;
      margin-bottom: 50px;

      div {
        font-size: 26px;
        padding: 6px 16px;
        background-color: #0b8d7e;
        width: 140px;
        color: #fff;
        border-radius: 6px;
        text-align: center;
        cursor: pointer;
        img {
          vertical-align: middle;
        }
      }
    }
  margin-top: 20px;
  .title {
    position: relative;
    b {
      position: absolute;
      width: 100%;
      text-align: center;
      left: 0;
    }
    span {
      position: absolute;
      left: 44px;
      z-index: 100;
      color: rgb(189, 187, 187);
      cursor: pointer;
    }
  }
  .scoll_context {
    height: calc(100vh - 200px);
    overflow-y: auto;
    margin-top: 20px;
    .rights_text {
      margin-left: 20px;
      font-weight: normal;
    }
    .miss_down {
      display: flex;
      justify-content: center;
      margin-top: 70px;
      margin-bottom: 50px;

      div {
        font-size: 26px;
        padding: 6px 16px;
        background-color: #0b8d7e;
        width: 120px;
        color: #fff;
        border-radius: 6px;
        text-align: center;
        cursor: pointer;
        img {
          vertical-align: middle;
        }
      }
    }
    .el-table .warning-row {
      background-color: #edfaf8;
    }

    .el-table .success-row {
      background: #fafafa;
    }
  }
  .item,
  .itemImg {
    display: flex;
    padding: 6px 30px 6px 30px;
    justify-content: space-between;
    box-sizing: border-box;
    div:nth-child(1) {
      font-weight: 600;
      padding-right: 100px;
      min-width: 160px;
    }
    .imgs {
      width: 100px;
      height: 100px;
      display: inline-block;
      margin-right: 4px;
    }
  }
  .itemImg {
    display: flex;
    flex-direction: column;
  }
  /deep/.el-col-12 {
    &:nth-child(1) {
      border-right: 1px solid rgb(233, 232, 232);
    }
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    min-height: 400px;
  }
  .infoSuccess_card {
    margin-bottom: 14px;
  }
}
</style>